@import url("https://fonts.googleapis.com/css2?family=Audiowide&family=Exo:wght@200&family=Libre+Barcode+128+Text&display=swap");

/*
font-family: 'Audiowide', cursive;
font-family: 'Exo', sans-serif;
font-family: 'Libre Barcode 128 Text', cursive;
*/

body {
  margin: 0;
  font-family: "Audiowide", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: rgb(16, 5, 34);
  color: wheat;
  cursor: url("./images/mse2.png") 9 9, auto;
  /* cursor: url("./images/mse3.png") 0 0, pointer; */
  /* cursor: url("./images/mse3.png"), wrap; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  cursor: url("./images/mse2.png") 9 9, auto;
  width: 10px;
}

::-webkit-scrollbar-track {
  /* border-radius: 6px;*/
  background-color: rgb(16, 5, 34);
  border: none;
}

::-webkit-scrollbar-thumb {
  border-radius: 6px;
  border: none;
  background-color: rgb(0, 52, 120);
}

::-webkit-scrollbar-thumb:hover {
  background-color: aqua;
}
