.FooterSuper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border-top: 2px solid rgb(255, 255, 255);
  box-shadow: 0 0 20px aqua, 0 0 40px aqua;
  background: black;
}

.FooterContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 40px 0px;
}

.FooterSocials {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 110px;
  padding: 20px 0px;
}

.FooterCR {
  font-family: "Libre Barcode 128 Text", cursive;
  font-size: 40px;
  color: white;
  text-align: center;
}

/*==============Media Queries===============*/

@media screen and (max-width: 700px) {
  .FooterCR {
    font-size: 27px;
  }
}
