.FSuper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 150px;
  color: white;
}

.Fcontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 40%;
}

.FTitle {
  padding: 40px 0px;
}

.FTitle h1 {
  text-shadow: 0 0 7px rgb(234, 0, 255), 0 0 15px rgb(234, 0, 255),
    0 0 25px rgb(234, 0, 255);
  color: rgb(16, 5, 34);
  font-size: 60px;
}

.WrapOpen {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background: linear-gradient(to right, black, rgb(63, 0, 105));
  padding: 0px 20px;
  /* cursor: pointer; */
  cursor: url("../../images/mse3.png") 9 9, pointer;
  border-top: solid 2px rgb(200, 0, 255);
  font-size: 9px;
  z-index: 10;
  transition: 0.3s ease-in-out;
  color: aqua;
}

.WrapClosed {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background: linear-gradient(
    to right,
    black,
    rgb(0, 60, 60),
    rgb(0, 177, 177)
  );
  padding: 0px 20px;
  /* cursor: pointer; */
  cursor: url("../../images/mse3.png") 9 9, pointer;
  border-top: solid 2px aqua;
  font-size: 9px;
  z-index: 10;
  transition: 0.3s ease-in-out;
}

.WrapClosed:hover {
  background: linear-gradient(to right, black, rgb(63, 0, 105));
  transition: 0.3s ease-in-out;
  border-top: solid 2px rgb(200, 0, 255);
}

.ArrowOpen {
  transition: 0.3s ease-in-out;
  transform: rotate(180deg);
  height: 30px;
}

.ArrowClosed {
  transition: 0.3s ease-in-out;
  height: 30px;
}

.DropOpen {
  background: linear-gradient(to right, black, rgb(63, 0, 105));
  width: 100%;
  padding: 5px 20px;
  max-height: 500px;
  transition: 0.5s ease-in-out;
  font-family: "Exo", sans-serif;
  font-size: 20px;
  /* cursor: pointer; */
  cursor: url("../../images/mse3.png") 9 9, pointer;
  color: aqua;
}

.DropClosed {
  background: rgb(26, 26, 26);
  width: 100%;
  padding: 5px 20px;
  max-height: 0px;
  transition: 0.5s ease-in-out;
  opacity: 0;
  font-family: "Exo", sans-serif;
  /* font-size: 0px; */
}

/*==============Media Queries===============*/

@media screen and (max-width: 1000px) {
  .Fcontainer {
    width: 80%;
  }
}

@media screen and (max-width: 600px) {
  .Wrap {
    font-size: 8px;
  }

  .FTitle h1 {
    font-size: 40px;
  }
}
