.HeroSuper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* height: 800px; */
  background-image: url("../../images/herobg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% auto;

  /* border: solid red 1px; */
}

.Herocontainer {
  padding: 12rem 0rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Herocontainer img {
  height: 300px;
}

.Herocontainer h1 {
  text-shadow: 0 0 7px aqua, 0 0 15px aqua, 0 0 25px aqua;
  color: rgb(16, 5, 34);
  font-size: 60px;
}

/*============Media Queries==============*/

@media screen and (max-width: 1200px) {
  .HeroSuper {
    background-size: auto 100%;
  }

  .Herocontainer {
    padding: 9rem 0rem;
  }
}

@media screen and (max-width: 700px) {
  .Herocontainer {
    padding: 7rem 0rem;
  }

  .Herocontainer img {
    height: 200px;
  }

  .Herocontainer h1 {
    text-shadow: 0 0 7px aqua, 0 0 15px aqua, 0 0 25px aqua;
    color: rgb(16, 5, 34);
    font-size: 30px;
  }
}
