.A1Super {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url(../../images/fondo1.png);
  background-position: center;
  box-shadow: 0 0 20px aqua, 0 0 40px aqua;
}

.A1Container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 70%;
  padding: 50px 0%;
}

.A1Left {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  text-align: justify;
  flex: 1;
  color: black;
}

.A1Left span {
  color: rgb(249, 135, 255);
}

.A1Left p {
  /* text-shadow: 0 0 5px rgb(255, 255, 255); */
  font-weight: bolder;
  color: white;
  text-shadow: 0 0 7px rgb(0, 0, 0), 0 0 15px rgb(0, 0, 0);
  font-family: "Exo", sans-serif;
}

.A1Left h1 {
  text-shadow: 0 0 7px rgb(0, 0, 0), 0 0 15px rgb(0, 0, 0);
  color: rgb(0, 234, 255);
  font-weight: 1000;
  text-align: right;
}

.A1Right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.A1Right img {
  height: 500px;
}

/*============Media Queries=============*/

@media screen and (max-width: 1000px) {
  .A1Container {
    width: 90%;
  }
}

@media screen and (max-width: 850px) {
  .A1Container {
    flex-direction: column;
    padding: 30px 0%;
  }

  .A1Left h1 {
    font-size: 24px;
  }

  .A1Right img {
    width: 290px;
    height: 290px;
  }
}
