.NavSuper {
  display: flex;
  justify-content: center;
  font-size: 18px;
  /* color: wheat; */
  background: rgba(0, 0, 0, 0.5);

  position: sticky;
  top: 0;
  z-index: 20;
}

.NavContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 75%;

  /* border: 1px solid rgb(255, 255, 255); */
}

.Logo {
  /* border: 1px solid rgb(253, 2, 2); */
}

.Logo img {
  height: 70px;
  /* cursor: pointer; */
  cursor: url("../../images/mse3.png") 9 9, pointer;
}

.Links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 50%;

  /* border: 1px solid rgb(47, 54, 255); */
}

.Links p {
  /* cursor: pointer; */
  cursor: url("../../images/mse3.png") 9 9, pointer;
  color: aqua;
  transition: 0.3s ease-in-out;
}

.Links p:hover {
  /* cursor: pointer; */
  cursor: url("../../images/mse3.png") 9 9, pointer;
  color: rgb(255, 255, 255);
  transition: 0.3s ease-in-out;
  text-shadow: 0 0 7px aqua, 0 0 10px aqua, 0 0 21px aqua, 0 0 42px aqua;
}

.Socials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 220px;

  /* border: 1px solid greenyellow; */
}

.Socials img {
  height: 40px;
  transition: 0.3s ease-in-out;
}

.Socials a {
  cursor: url("../../images/mse3.png") 9 9, pointer;
}

.Socials img:hover {
  transition: 0.3s ease-in-out;
  transform: scale(1.1);
  filter: invert(50%) sepia(100%) hue-rotate(170deg) saturate(10);
}

/*=========Mint Button==========*/

.MintBtn a {
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  color: aqua;
  text-decoration: none;
  font-size: 20px;
  overflow: hidden;
  transition: 0.1s;
  /* border: solid aqua 2px; */
}

.MintBtn a:hover {
  color: rgb(16, 5, 34);
  background: aqua;
  box-shadow: 0 0 10px aqua, 0 0 20px aqua, 0 0 40px aqua;
  transition-delay: 0.3s;
  /* border: solid transparent 2px; */
}

.MintBtn a span {
  position: absolute;
  display: block;
}

.MintBtn a span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, aqua);
}

.MintBtn a:hover span:nth-child(1) {
  left: 100%;
  transition: 0.3s;
}

.MintBtn a span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, aqua);
}

.MintBtn a:hover span:nth-child(3) {
  right: 100%;
  transition: 0.3s;
}

.MintBtn a span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, aqua);
}

.MintBtn a:hover span:nth-child(2) {
  top: 100%;
  transition: 0.3s;
  transition-delay: 0.1s;
}

.MintBtn a span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, aqua);
}

.MintBtn a:hover span:nth-child(4) {
  bottom: 100%;
  transition: 0.3s;
  transition-delay: 0.1s;
}

.Movil {
  position: absolute;
  width: 0%;
  min-width: 0px;
  pointer-events: none;
  opacity: 1;
  right: 40px;

  /* border: 1px solid rgb(255, 47, 217); */
}

.Movil img {
  width: 0px;
}

.NavBtn {
  background: none;
  border: none;
}

/*=========== Media Queries ===========*/

@media screen and (max-width: 1200px) {
  .NavContainer {
    width: 90%;
  }
}

@media screen and (max-width: 850px) {
  .NavSuper {
    justify-content: left;
  }

  .NavContainer {
    width: calc(50% + 50px);
    justify-content: space-between;
  }

  .Links {
    pointer-events: none;
    opacity: 0;
    width: 0%;
    min-width: 0px;
    font-size: 0px;

    /* border: 1px solid rgb(47, 54, 255); */
  }

  .Socials {
    pointer-events: auto;
    opacity: 1;
    width: 95px;
    min-width: 0px;

    /* border: 1px solid greenyellow; */
  }

  .Socials img {
    width: 0%;
    pointer-events: none;
  }

  /* .MintBtn a {
    opacity: 0;
    pointer-events: none;
  } */

  .Movil {
    position: absolute;
    width: 40px;
    pointer-events: auto;
    opacity: 1;
    right: 40px;

    /* border: 1px solid rgb(255, 47, 217); */
  }

  .Movil img {
    width: 50px;
    height: 50px;
  }
}

/*============Panel Movil============*/

.NavPanel-Closed {
  position: absolute;
  top: 60px;
  width: 95%;
  background: rgb(16, 5, 34);
  opacity: 0;
  pointer-events: none;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.NavPanel-Closed p {
  color: black;
}

.NavPanel-Open {
  position: absolute;
  top: 90px;
  width: 95%;
  background: rgb(16, 5, 34);
  opacity: 0;
  pointer-events: none;
  border: solid 2px white;
  border-radius: 10px;
  box-shadow: inset 0 0 40px aqua, 0 0 40px aqua;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 10% 0%;
}

.NavPanel-Open p {
  color: aqua;
}

.NavPanel-Open p:hover {
  color: white;
  text-shadow: 0 0 7px aqua, 0 0 10px aqua, 0 0 21px aqua, 0 0 42px aqua;
}

@media screen and (max-width: 850px) {
  .NavPanel-Closed {
    position: absolute;
    top: -60px;
    width: 95%;
    background: rgb(9, 53, 9);
    opacity: 0;
    pointer-events: none;
    transition: 0.3s ease-in-out;
  }

  .NavPanel-Open {
    opacity: 1;
    pointer-events: auto;
    transition: 0.3s ease-in-out;
  }
}

.PanelLinks {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0% 0%;
  padding-bottom: 10%;
}

.PanelSocials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 120px;
}
